















































.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  .content {
    height: 100%;
    .item {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #ccc;
      font-size: 5rem;
      text-align: center;
      line-height: 30vh;
    }
  }
  .dot {
    // width: 30%;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    span {
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: rgba(0,0,0,.3);
      color: #fff;
      text-align: center;
      transition: background-color 1s, color 1s;
      margin:5px;
      &.active {
        background-color: rgb(0, 162, 255);
        color: #fff;
      }
    }
  }
} 
/* left */
.toLeft-enter {
  transform: translateX(100%);
}

.toLeft-enter-to {
  transform: translateX(0);
  transition: transform 500ms;
}

.toLeft-leave {
  transform: translateX(0);
}

.toLeft-leave-to {
  transform: translateX(-100%);
  transition: transform 500ms;
}
 
